* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #121212;
  height: 100%;
}

.tv-show-detail-container {
  height: auto; /* auto로 변경 */
  flex-direction: column; /* 세로 방향으로 배치 */
  align-items: center; /* 가운데 정렬 */
  color: #f0eded; /* 글자 색상 */
  background-color: #7B7777;
  padding: 20px; /* 전체 컨테이너 여백 */
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.tv-shows {
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
  padding: 50px;
  width: 80%;
  padding-top: 70px;
}

@media screen and (max-width: 1090px) {
  .tv-shows {
    grid-template-columns: 1fr;
    width: 100%;
  }
}

.cast-container {
  display: flex;
  overflow-x: auto; /* 수평 스크롤 가능 */
  padding: 10px;
}

.cast-member {
  margin-right: 10px; /* 간격 추가 */
  text-align: center; /* 텍스트 가운데 정렬 */
}

.cast-image {
  width: 100px; /* 캐스팅 사진의 너비 조정 */
  height: 150px; /* 기본 이미지 높이 설정 */
  border-radius: 5px; /* 이미지 모서리 둥글게 */
  object-fit: cover; /* 이미지를 박스에 맞게 잘라줌 */
}

.back-button {
  background-color: #007bff; /* 버튼 배경색 */
  color: white; /* 글자색 */
  border: none; /* 테두리 없음 */
  padding: 10px 20px; /* 패딩 */
  border-radius: 5px; /* 모서리 둥글게 */
  cursor: pointer; /* 포인터 모양 */
  font-size: 16px; /* 글자 크기 */
  margin-bottom: 100px; /* 아래 여백 */
  display: block; /* 블록 요소로 변경 */
  text-align: center; /* 텍스트 중앙 정렬 */
}

.back-button:hover {
  background-color: #0056b3; /* 호버 시 배경색 변경 */
}
